<template>
  <div class="product">
    <Swiper></Swiper>
    <div class="content">
      <div class="title">
        <p>产品服务</p>
      </div>
      <div v-for="(category, index) in list" :key="index" class="product-category">
        <p>{{category.category}}</p>
        <div class="product-content">
          <div class="product-menu">
            <ul class="menu">
              <li v-for="(product, index) in category.products" :key="index">
                <div :class="index==0?'menu-category menu-first':'menu-category'">
                  <span>{{product.title}}</span>
                </div>
              </li>
            </ul>
          </div>
          <div class="product-img">
            <ul class="detail">
              <li v-for="(product, index) in category.products" :key="index">
                <div class="img">
                  <a>
                    <img :src="product.mainImgUrl" />
                    <p>{{product.title}}</p>
                  </a>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
const json = require("/static/products/products.json");
import Swiper from "./../product/Swiper";
export default {
  components: {
    Swiper
  },
  data() {
    return {
      list: json
    };
  },
  created: function() {
    console.log(this.list);
  }
};
</script>
<style lang="less" scoped>
.product {
  text-align: center;
  .product-img {
    text-align: center;
    margin-bottom: 50px;
    padding: 0;
    img {
      margin: 0 auto;
      display: block;
      width: 100%;
      height: 300px;
      object-fit: cover;
    }
  }
  .content {
    padding: 10px 160px;
    .title p {
      color: #3773dc;
      font-family: 微软雅黑;
      font-size: 30px;
    }
    .product-category {
      .product-content {
        padding: 0 50px;
        display: flex;
        p {
          color: #858585;
        }
        .product-menu {
          padding: 0 20px;
          .menu {
            width: 210px;
            text-align: left;
            list-style-type: none;
            margin: 0px auto 20px;
            padding-left: 0px;
            .menu-category {
              background: #dedede;
              cursor: pointer;
              display: block;
              position: relative;
              border-bottom: 1px solid #ffffff;
              span {
                padding: 10px;
                color: #666;
                font-size: 14px;
                line-height: 270%;
              }
            }
            li.open {
              color: #b63b4d;
            }
            .sub-menu {
              list-style-type: none;
              background: #f6f6f6;
              font-size: 14px;
              line-height: 180%;
              padding-left: 0px;
              li {
                margin: 0;
                padding-left: 10px;
                a {
                  display: block;
                  text-decoration: none;
                  color: #4d4d4d;
                }
              }
            }
          }
        }
        .product-img {
          padding-right: 50px;
          .detail {
            margin: auto;
            padding: 0;
            display: flex;
            flex-wrap: wrap;
            list-style-type: none;
            li {
              vertical-align: top;
              border-width: 3px;
              .img {
                img {
                  padding: 0 20px;
                  width: 200px;
                  height: 200px;
                  object-fit: cover;
                }
                p {
                  width: 200px;
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>


